import { v4 as uuidv4 } from 'uuid';

type PageViewEvent = {
  path: string;
  title?: string;
  referrer?: string;
};

type PurchaseEvent = {
  transaction_id: string;
  value: number;
  currency: string;
  items?: Array<{
    item_id: string;
    item_name: string;
    price: number;
    quantity: number;
  }>;
};

type EventData = {
  type: string;
  timestamp: number;
  target?: EventTarget | null;
  details?: Record<string, any>;
};

type TrackerOptions = {
  currency?: string;
  samplingRate?: number;
  debug?: boolean;
};

type UserInfo = {
  id?: string;
  properties?: Record<string, any>;
};

function getCookie(name: string): string | null {
  const cookieMatch = document.cookie.match(`(^|;)\\s*${name}\\s*=\\s*([^;]+)`);
  return cookieMatch ? decodeURIComponent(cookieMatch[2]) : null;
}

function setCookie(name: string, value: string, days: number = 365) {
  const date = new Date();
  date.setTime(date.getTime() + days * 24 * 60 * 60 * 1000);
  const expires = `expires=${date.toUTCString()}`;
  document.cookie = `${name}=${encodeURIComponent(
    value
  )};${expires};path=/;SameSite=Strict`;
}

function eventToSearchParams(event: EventData): URLSearchParams {
  const params = new URLSearchParams();

  // Basic event properties
  params.set('type', event.type);
  params.set('timestamp', event.timestamp.toString());

  // Add details if exists
  if (event.details) {
    Object.entries(event.details).forEach(([key, value]) => {
      params.set(
        key,
        typeof value === 'object' ? JSON.stringify(value) : String(value)
      );
    });
  }

  // Optional target information
  if (event.target instanceof HTMLElement) {
    params.set('target_tag', event.target.tagName);
    params.set('target_id', event.target.id);
  }

  return params;
}

function createTracker(
  trackerId?: string,
  options: TrackerOptions = {}
): Tracker {
  const events: EventData[] = [];
  let originalDataLayerPush: Function | null = null;
  let userInfo: UserInfo = {};

  // Generate or retrieve tracker ID
  const id = trackerId || getCookie('_aft') || uuidv4();
  setCookie('_aft', id);

  const log = (message: string, data?: any) => {
    if (options.debug) {
      console.log(`[Tracker ${id}]`, message, data || '');
    }
  };

  const recordEvent = (event: EventData) => {
    const enrichedEvent = {
      ...event,
      trackerId: id,
      user: userInfo,
      options: {
        currency: options.currency,
        samplingRate: options.samplingRate,
      },
    };
    const query = eventToSearchParams(enrichedEvent);
    const url = 'https://analytics.affisto.com/v1/collect';
    // const url = 'http://localhost:8080/v1/collect';
    fetch(`${url}?${query.toString()}`);
    events.push(enrichedEvent);
    log('Tracked Event:', enrichedEvent);
  };

  const track = (eventName: string, details?: Record<string, any>) => {
    const customEvent: EventData = {
      type: `custom:${eventName}`,
      timestamp: Date.now(),
      details: details || {},
    };
    recordEvent(customEvent);
  };

  const setUserId = (userId: string, properties?: Record<string, any>) => {
    userInfo = { id: userId, properties };
    track('user_identified', { userId, ...properties });
  };

  if (window.dataLayer) {
    originalDataLayerPush = window.dataLayer.push;
    window.dataLayer.push = (...args: any[]) => {
      const event = args[0];
      if (event && typeof event === 'object') {
        // track('ga:event', { originalEvent: event, details: args.slice(1) });
      }
      return originalDataLayerPush?.apply((window as any).dataLayer, args);
    };
  }

  return {
    id,
    track,
    setUserId,
    restore: () => {
      if (originalDataLayerPush) {
        window.dataLayer.push = originalDataLayerPush;
      }
    },
  };
}

export { createTracker };
export type { EventData, PageViewEvent, PurchaseEvent, TrackerOptions };
